/* Small screens */ /* Medium screens */ /* Large screens */

:root {
  scroll-behavior: smooth;
  font-size: 16px;
  /* font-family: "Rubik", sans-serif;  */
  font-family: "Raleway", sans-serif;
  letter-spacing: 0.2rem;
  font-weight: 700;
  background-color: #eeeeee;
  --text-primary: #b6b6b6;
  --text-secondary: #ececec;
  --bg-primary: #23232e;
  --bg-secondary: #141418;
  --transition-speed: 600ms;
}
#root,
html,
body {
  min-width: 100%;
  min-height: 100%;
  margin: 0;
  padding: 0;
}

.show-case-container {
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
}
.show-case-child {
  /* background-color: aquamarine; */
  width: 25%;
  height: 150px;
  padding: 0;
  margin: 0;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% 100%;
  transition: transform 0.8s ease;
}
.show-case-child:hover {
  transform: scale(
    1.5
  ); /* (150% zoom - Note: if the zoom is too large, it will go outside of the viewport) */
}
.show-case-inner-child {
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
  /* background-color: rgba(25, 30, 50, 0.5); /* SAVE!!! */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.logo {
  /* height: 25%; */
  width: 60%;
  padding: 0;
  margin: 0;
}

